import Axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react';



function App() {
  const [location, setlocation] = useState('ahmedabad');
  const [weather, setweather] = useState({})


  useEffect(() => {
    Axios.get(`http://api.weatherstack.com/current?access_key=7e7c309ab455a71471c68ff12a36d13e&query=${location}`)
      .then(res => {
        setweather(res);
      })
      .catch(err => {
        alert(err.error.info)
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getDataFromAPI = e => {
    if (e.key === 'Enter') {
      Axios.get(`http://api.weatherstack.com/current?access_key=7e7c309ab455a71471c68ff12a36d13e&query=${location}`)
        .then(res => {
          setweather(res);
          setlocation('');
        })
        .catch(err => {
          alert(err.error.info)
        })
    }
  }

  const dateGenerator = () => {
    const date = new Date();
    return date.toUTCString().split(" ", 4).join(" ");
  }


  return (

    Object.keys(weather).length ?
      <div className={weather.data.current.temperature > 30 ? new Date().getHours() > 18 ? 'App warm  night' : "App warm" : new Date().getHours() > 18 ? "App night " : 'App'}>

        <main>
          <div className="searchBox">
            <input
              type="text"
              className="searchBar"
              onChange={(e) => setlocation(e.target.value)}
              value={location}
              onKeyPress={(e) => getDataFromAPI(e)}
              placeholder="Find your city" />
          </div>

          <Fragment>
            <div className="locationBox">
              <div className="location"> {weather.data.location.name} , {weather.data.location.country}</div>
              <div className="date">{dateGenerator()}</div>
            </div>
            <div className="weatherBox">
              <img src={weather.data.current.weather_icons[0]} alt={weather.data.current.weather_descriptions} className="icon" />
              <div className="temp"> {weather.data.current.temperature}Â°c</div>
              <div className="weather">{weather.data.current.weather_descriptions}</div>
            </div>
          </Fragment>
        </main>
      </div>

      : ''
  );
}

export default App;
